.breadcrumbs-container {
  margin: 12.75px;
  height: 20px;
  padding-left: 242px;
  list-style: none;
}

.breadcrumbs {
  font-size: 0.875rem;
  color: #666;
  font-weight: 400;
  line-height: 1.5;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.breadcrumbs a {
  color: #666;
  text-decoration: none;
}

@media screen and (max-width: 800px) {
  .breadcrumbs-container {
    padding-left: 0;
  }
}
